___CSS_LOADER_EXPORT___.push([module.id, `.input-container {
    position: relative;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'avenir next', avenir,
        'helvetica neue', helvetica, ubuntu, roboto, noto, 'segoe ui', arial,
        sans-serif;
}
.active-suggestion {
    background-color: #1d70b8 !important;
    color: white;
}

.suggestion {
    display: block;
    height: 30px;
    border-bottom: 1px solid black;
    padding-left: 5px;
    padding-top: 10px;
}

.suggestions {
    background-color: #fff;
    border: 2px solid #0b0c0c;
    border-top: 0;
    color: #0b0c0c;
    margin: 0;
    max-height: 500px;
    overflow-x: hidden;
    padding: 0;
    width: 100%;
    width: calc(100% - 4px);
}

.odd-suggestion {
    background-color: #f3f2f1;
}

.dual-input {
    width: 100%;
    height: 40px;
    border: 2px solid black;
    font-family: Arial, Helvetica, sans-serif;
    padding-right: 30px;
    font-size: 18px;
}

.clear-button {
    position: absolute;
    top: 50%;
    right: 10px; /* Adjust the position of the button */
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer !important;
    display: block;
    font-size: 24px;
}

.clear-button:hover {
    color: darkgray;
}

.focused {
    outline: 3px solid #fd0;
}
