function getPostcodes(s) {
    const apiUrl = `https://api.postcodes.io/postcodes/${s}/autocomplete`;
    return fetch(apiUrl)
        .then((response) => {
            // Check if response is successful
            if (!response.ok) {
                throw new Error('Network response was not ok');
            }
            // Parse response JSON
            return response.json();
        })
        .then((data) => {
            // Do something with the data
            return data.result;
        })
        .catch((error) => {
            // Handle errors
            console.error(
                'There was a problem with the fetch operation:',
                error
            );
        });
}

export default getPostcodes;
