import getPostcodes from './getPostcodes';

function getSuggestions(value, options, setSuggestions) {
    // If numeric it is likely a postcode
    const containsNumeric = /\d/.test(value);

    if (containsNumeric) {
        // Postcode suggestions
        getPostcodes(value).then((postcodes) => {
            let postcodeSuggestions = postcodes.map((postcode) => ({
                label: postcode,
                value: postcode,
            }));
            setSuggestions(postcodeSuggestions);
        });
    } else {
        // Options suggestions
        const filteredOptions = options.filter((option) =>
            option['label'].startsWith(value)
        );
        setSuggestions(filteredOptions);
    }
}

export default getSuggestions;
