import React from 'react';
import PropTypes from 'prop-types';
import {useState} from 'react';

import getSuggestions from '../getSuggestions';
import './styles.css';

function DualInput({id, options, value, setProps, label}) {
    // Define the state of the component
    const [userInput, setUserInput] = useState('');
    const [activeSuggestion, setActiveSuggestion] = useState(0);
    const [suggestions, setSuggestions] = useState([]);
    const [showSuggestions, setShowSuggestions] = useState(false);
    const [showPrompt, setShowPrompt] = useState(false);
    const [focus, setFocus] = useState(false);

    // Initial value popoulated
    if (value.length > 0) {
        setUserInput(value);
    }

    // When a user types into the input
    function onChange(e) {
        const value = e.target.value;
        setUserInput(e.target.value);

        // This needs to be extracted into a function and called onFocus too.
        if (value.length >= 3) {
            setShowPrompt(false);
            setShowSuggestions(true);
            getSuggestions(value, options, setSuggestions);
        } else {
            // Reset suggestions when value is less than 3 characters
            setShowPrompt(true);
            setSuggestions([]);
            setShowSuggestions(false);
            setActiveSuggestion(0);
        }
    }

    // Handle keyboard interactiosn
    function onKeyDown(e) {
        // User pressed the enter key
        if (e.keyCode === 13) {
            setUserInput(suggestions[activeSuggestion]['label']);
            setProps({
                value: suggestions[activeSuggestion]['value'],
                label: suggestions[activeSuggestion]['label'],
            });
            setShowSuggestions(false);
            setActiveSuggestion(0);
        }

        // User pressed the up arrow
        else if (e.keyCode === 38) {
            if (activeSuggestion === 0) {
                // There is nowhere higher to go
                return;
            }
            // Move active suggestion up one
            setActiveSuggestion(activeSuggestion - 1);
        }

        // User pressed the down arrow
        else if (e.keyCode === 40) {
            if (activeSuggestion + 1 === suggestions.length) {
                // There is nowhere lower to go, reset to top
                setActiveSuggestion(0);
                return;
            }
            // Move active suggestion down one.
            setActiveSuggestion(activeSuggestion + 1);
        }

        // User pressed the tab button and wants to move away
        else if (e.key === 'Tab') {
            setShowSuggestions(false);
        }
    }

    // Handle clicks
    function handleOptionClick(e) {
        e.preventDefault();
        setUserInput(e.currentTarget.dataset.label);
        setProps({
            value: e.currentTarget.dataset.value,
            label: e.currentTarget.dataset.label,
        });
        setShowSuggestions(false);
    }

    // On focus of the input box
    function onFocus(e) {
        e.preventDefault();
        setFocus(true);
        // This is where we need to call the suggestions code again
        const value = e.target.value;
        if (value.length < 3) {
            setShowPrompt(true);
        } else {
            setShowPrompt(false);
            setShowSuggestions(true);
            getSuggestions(value, options, setSuggestions);
        }
    }

    // When user blurs from input box
    function onBlur() {
        setFocus(false);
    }

    // When user hovers over option
    function onMouseEnter(e) {
        const liElement = e.target;
        const key = parseInt(liElement.getAttribute('data-key'));

        setActiveSuggestion(key);
    }

    // When user clicks clear button
    function clearInput() {
        setUserInput('');
        setShowSuggestions(false);
    }

    // Code to allow clicking away from the component
    const handleClickOutside = (e) => {
        if (!e.target.closest('.input-container')) {
            setShowSuggestions(false);
        }
    };

    // Handles when a user clicks away from the components
    React.useEffect(() => {
        document.body.addEventListener('click', handleClickOutside);
        return () => {
            document.body.removeEventListener('click', handleClickOutside);
        };
    }, []);

    // Return the component JSX
    return (
        <div>
            <div className="input-container">
                <input
                    id={id}
                    className={`dual-input ${focus ? 'focused' : ''}`} // Toggle focus styling
                    onKeyDown={onKeyDown}
                    autoComplete="off"
                    placeholder="Enter a postcode or local authority..."
                    value={userInput}
                    onChange={onChange}
                    onFocus={onFocus}
                    onBlur={onBlur}
                    aria-haspopup="listbox"
                    aria-autocomplete="both"
                    aria-controls="suggestions-list"
                    aria-expanded={showSuggestions}
                    aria-describedby="dualInput__assistiveHint"
                    tabIndex="0"
                ></input>
                {userInput && (
                    <button
                        className="clear-button"
                        onClick={clearInput}
                        aria-label="Clear input"
                    >
                        x
                    </button>
                )}
            </div>

            {showPrompt && (
                <div className="dual-input-prompt">
                    <ul className="suggestions">
                        <li className="suggestion odd-suggestion">
                            Enter 3 or more characters for suggestions
                        </li>
                    </ul>
                </div>
            )}

            {showSuggestions && (
                <ul
                    className="suggestions"
                    aria-label="Suggestions"
                    role="listbox"
                    id="suggestion-list"
                >
                    {suggestions.map((suggestion, index) => {
                        let className = 'suggestion';

                        if (index === activeSuggestion) {
                            className = `${className} active-suggestion`; // Toggle active suggestion styling
                        }
                        if (index % 2 !== 0) {
                            className = `${className} odd-suggestion`; // Toggle odd stylinh
                        }
                        return (
                            <li
                                key={index}
                                data-key={index}
                                className={className}
                                onMouseDown={(event) =>
                                    handleOptionClick(event)
                                } // Had to use onMouseDown rather than onClick. Wasn't working in dash.
                                onMouseEnter={(event) => onMouseEnter(event)}
                                aria-selected={index == activeSuggestion}
                                aria-label={suggestion}
                                role="option"
                                aria-posinset={index}
                                data-value={suggestion['value']}
                                data-label={suggestion['label']}
                                option-value={suggestion['value']}
                                tabIndex="-1"
                            >
                                {suggestion['label']}
                            </li>
                        );
                    })}
                </ul>
            )}
            <span id="dualInput__assistiveHint" style={{display: 'none'}}>
                Enter 3 or more characters for suggestions.
            </span>
        </div>
    );
}

DualInput.propTypes = {
    id: PropTypes.string,
    label: PropTypes.string,
    options: PropTypes.array,
    setProps: PropTypes.func,
    value: PropTypes.string,
};
DualInput.defaultProps = {
    id: 'dual_input',
    options: [],
    setProps: () => {},
    value: '',
    label: '',
};

export default DualInput;
